          <span class="token punctuation">{</span> label<span class="token operator">:</span> <span class="token string">"美国"</span><span class="token punctuation">,</span> value<span class="token operator">:</span> <span class="token string">"american"</span> <span class="token punctuation">}</span><span class="token punctuation">,</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
